<template>
  <div>
    <div>
      <div class="background"></div>
      <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
        <h1>ADCL - CROSS-CHECK</h1>
        <h3 class="mb-5 text-danger fw-bold">WARNING!!! UNDER TEST</h3>

        <div class="inputs-container">
          <div class="excel-selector-containers-cc">
            <h4 class="mb-4">AD statuses</h4>
            <div class="file-input-container">
                <p>
                  Select the excel file:
                </p>
                <input id="old-version" class="form-control" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .xlsm" @change="readFile('old-version')">
            </div>
            <div class="worksheet-selector-container">
                <p>
                  Select the sheet:
                </p>
                <select :class="[workbookOldVersion === '' ? 'disabled-select' : '']" :disabled="workbookOldVersion === '' ? true : false" v-model="worksheetOldVersion">
                  <option value="" v-if="worksheetOldVersion.length === 0"><p>-- select an option --</p></option>
                  <option v-for="(sheetName, index) in workbookOldVersion.SheetNames" :key="index" :value="sheetName">{{ sheetName }}</option>
                </select>
            </div>
          </div>


          <div class="excel-selector-containers-cc">
            <h4 class="mb-4">ADCL update</h4>
            <div class="file-input-container">
                <p>
                  Select the excel file:
                </p>
                <input id="new-version" class="form-control" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .xlsm" @change="readFile('new-version')">
            </div>
            <div class="worksheet-selector-container">
                <p>
                  Select the sheet:
                </p>
                <select :class="[workbookNewVersion === '' ? 'disabled-select' : '']" :disabled="workbookNewVersion === '' ? true : false" v-model="worksheetNewVersion">
                  <option value="" v-if="worksheetNewVersion.length === 0"><p>-- select an option --</p></option>
                  <option v-for="(sheetName, index) in workbookNewVersion.SheetNames" :key="index" :value="sheetName">{{ sheetName }}</option>
                </select>
            </div>
          </div>
        </div>

        <div class="buttons-container">
          <button class="btn btn-primary" :class="[this.worksheetOldVersion === '' || this.worksheetNewVersion === '' ? 'disabled' : '']" @click="transformFile">CROSS-CHECK</button>
        </div>
      </div>

    </div>
    <div class="result-container" v-if="showResults">
      <div class="download-container" v-if="results.length != 0">
        <!-- <span class="icon icon-24 icon-excel-black"></span> -->
        <span class="icon icon-24 icon-excel-green" @click="downloadFile" data-toggle="tooltip" title="Download in Excel"></span>
      </div>
      <h4 class="mb-5">CROSS-CHECK RESULT FOR <strong>{{ worksheetDisplayed }}</strong> TAB:</h4>
      <table class="table-cc" v-if="results.length != 0">
        <tr class="table-cc">
          <th class="table-cc" data-toggle="tooltip" title="Type of action of the finding (added, removed or modified)">ACTION</th>
          <th class="table-cc">AD REF</th>
          <th class="table-cc">EQUIVALENT AD</th>
          <th class="table-cc" data-toggle="tooltip" title="Line in the excel file where this can be find">EXCEL SHEET LINE</th>
          <th class="table-cc" data-toggle="tooltip" title="Line number inside the AD REF group">AD GROUP LINE</th>
          <th class="table-cc" data-toggle="tooltip" title="Specific column where the modification has been found (only when ACTION is 'modified')">COLUMN</th>
        </tr>
        <tr class="table-cc" v-for="row,index in results" :key="index">
          <td class="table-cc">{{ row[0] }}</td>
          <td class="table-cc">{{ row[1] }}</td>
          <td class="table-cc">{{ row[2] }}</td>
          <td class="table-cc">{{ row[4] }}</td>
          <td class="table-cc">{{ parseInt(row[3])+1 }}</td>
          <td class="table-cc">{{ row.length == 6 ? row[5] : "" }}</td>
        </tr>
      </table>
      <div v-else>
        No differance has been found on the cross-check.
      </div>
    </div>

  </div>
</template>


<script>
const XLSX = require("xlsx");

export default {
  name: 'CrossCheck',
  data() {
    return {
      // Importing excel file data
      workbookOldVersion: "",
      worksheetOldVersion: "",
      workbookNewVersion: "",
      worksheetNewVersion: "",
      download_workbook: "",
      fileName: "",

      showResults: false,
      worksheetDisplayed: "",
      added: [],
      removed: [],
      modified: [],
    }
  },
  computed: {
    results() {
      return [
        ...this.added,
        ...this.removed,
        ...this.modified,
      ]
    }
  },
  methods: {
    readFile(id) {
      const input = document.querySelector("#" + id);
      this.file = input.files[0];
      if(id.includes("new")) this.fileName = this.file.name.split(".").slice(0,-1).join(".");
      const reader = new FileReader();

      reader.onload = (e) => {
        // Parse data
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        if(id.includes("old")) this.workbookOldVersion = wb;
        if(id.includes("new")) this.workbookNewVersion = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    transformFile() {
      // Select the excel worksheet
      const sheetNameOld = this.worksheetOldVersion;
      const sheetOld = this.workbookOldVersion.Sheets[sheetNameOld];
      
      const sheetNameNew = this.worksheetNewVersion;
      const sheetNew = this.workbookNewVersion.Sheets[sheetNameNew];

      // Transform fron excel to JSON
      const header = Array.apply(null, Array(18)).map((_,i) => `col${i}`);
      const arrOld = XLSX.utils.sheet_to_json(sheetOld, {
        header: header,
      });
      
      const arrNew = XLSX.utils.sheet_to_json(sheetNew, {
        header: header,
      });

      // Get the location of the table's header
      let headerIndexOld = 0;
      while(headerIndexOld < 10) {
        if(Object.values(arrOld[headerIndexOld]).includes("AD REF") || Object.values(arrOld[headerIndexOld]).includes("EUAD")) break;
        headerIndexOld++;
      };
      
      let headerIndexNew = 0;
      while(headerIndexNew < 10) {
        if(Object.values(arrNew[headerIndexNew]).includes("AD REF") || Object.values(arrNew[headerIndexNew]).includes("EUAD")) break;
        headerIndexNew++;
      };

      // Separete the data into header, table's header, and the data itself
      let tableDataOld;
      if(Object.values(arrOld.slice(-1)[0])[0].toLowerCase() === "end of report") { // Remove the "end of report" row (last row)
        tableDataOld = arrOld.slice(headerIndexOld+3 ,-1);
      } else {
        tableDataOld = arrOld.slice(headerIndexOld+3);
      };
      
      let tableDataNew;
      if(Object.values(arrNew.slice(-1)[0])[0].toLowerCase() === "end of report") { // Remove the "end of report" row (last row)
        tableDataNew = arrNew.slice(headerIndexNew+3 ,-1);
      } else {
        tableDataNew = arrNew.slice(headerIndexNew+3);
      };

      function colNumberToCorrectName(col) {
        switch(col) {
          case "col1":
            return "AD REF";
          case "col2":
            return "EQUIVALENT AD";
          case "col3":
            return "SUBJECT";
          case "col4":
            return "APPLICABILITY";
          case "col5":
            return "EFFECTIVE DATE";
          case "col6":
            return "METHOD OF COMPLIANCE";
          case "col7":
            return "LD DATE";
          case "col8":
            return "LD FH";
          case "col9":
            return "LD FC";
          case "col10":
            return "ND DATE";
          case "col11":
            return "ND FH";
          case "col12":
            return "ND FC";
          case "col13":
            return "REMARKS";
          case "col14":
            return "AD CLOSED";
          default:
            return "";
        }
      }

      let currentId = [];
      let currentIdOld = [];

      const newAD = [];
      const modifiedAD = [];
      const removedAD = [];
      Object.keys(tableDataNew).forEach(k => {
        if(tableDataNew[k]["col1"] || tableDataNew[k]["col2"]) {
          currentId = [
            tableDataNew[k]["col1"] ? String(tableDataNew[k]["col1"]).replaceAll(/\r\n/g, "").trim() : "",
            tableDataNew[k]["col2"] ? String(tableDataNew[k]["col2"]).replaceAll(/\r\n/g, "").trim() : "",
            0
          ];
        } else {
          currentId[2]++;
        }
        let found = false;
        Object.keys(tableDataOld).forEach(oldK => {
          if(tableDataOld[oldK]["col1"] || tableDataOld[oldK]["col2"]) {
            currentIdOld = [
              tableDataOld[oldK]["col1"] ? String(tableDataOld[oldK]["col1"]).replaceAll(/\r|\n/g, "").trim() : "",
              tableDataOld[oldK]["col2"] ? String(tableDataOld[oldK]["col2"]).replaceAll(/\r|\n/g, "").trim() : "",
              0
            ];
          } else {
            currentIdOld[2]++;
          }
          if((currentId[0] == currentIdOld[0] && currentId[1] == currentIdOld[1] && currentId[2] == currentIdOld[2]) ||
            (currentId[0] != "" && currentId[0] == currentIdOld[0] && currentId[2] == currentIdOld[2])) {
            found = true;

            Object.keys(tableDataNew[k]).forEach(col => {
              const colName = colNumberToCorrectName(col);
              if(colName != "") {
                const colValue = tableDataNew[k][col] ? String(tableDataNew[k][col]).replaceAll(/\r|\n|\s|^"|"$/g, "").trim() : "";
                const colValueOld = tableDataOld[oldK][col] ? String(tableDataOld[oldK][col]).replaceAll(/\r|\n|\s|^"|"$/g, "").trim() : "";
                if(colValue != colValueOld) {
                  modifiedAD.push(["modified", ...currentId, parseInt(k) + 11, colName])
                }
              }
            })
          }
        })
        if(!found && !currentId.includes(undefined)) {
          newAD.push(["added", ...currentId, parseInt(k) + 11]); // 11 the row where the datatable begins in the excel
        }
      })

      Object.keys(tableDataOld).forEach(k => {
        if(tableDataOld[k]["col1"] || tableDataOld[k]["col2"]) {
          currentId = [
            tableDataOld[k]["col1"] ? String(tableDataOld[k]["col1"]).replaceAll(/\r\n/g, "").trim() : "",
            tableDataOld[k]["col2"] ? String(tableDataOld[k]["col2"]).replaceAll(/\r\n/g, "").trim() : "",
            0
          ];
        } else {
          currentId[2]++;
        }
        let found = false;
        Object.keys(tableDataNew).forEach(newK => {
          if(tableDataNew[newK]["col1"] || tableDataNew[newK]["col2"]) {
            currentIdOld = [
              tableDataNew[newK]["col1"] ? String(tableDataNew[newK]["col1"]).replaceAll(/\r|\n/g, "").trim() : "",
              tableDataNew[newK]["col2"] ? String(tableDataNew[newK]["col2"]).replaceAll(/\r|\n/g, "").trim() : "",
              0
            ];
          } else {
            currentIdOld[2]++;
          }
          if((currentId[0] == currentIdOld[0] && currentId[1] == currentIdOld[1] && currentId[2] == currentIdOld[2]) ||
            (currentId[0] != "" && currentId[0] == currentIdOld[0] && currentId[2] == currentIdOld[2])) {
            found = true;
          }
        })
        if(!found && !currentId.includes(undefined)) {
          // if(currentId[1].length !== 0) {
            
          // }
          removedAD.push(["removed", ...currentId, parseInt(k) + 11]); // 11 the orw where the datatable begins in the excel
        }
      })
      // console.log(newAD)
      // console.log(modifiedAD)
      // console.log(removedAD)
      this.added = newAD;
      this.modified = modifiedAD;
      this.removed = removedAD;
      this.worksheetDisplayed = this.worksheetNewVersion.toUpperCase();

      this.createDownloadFile();
      this.showResults = true;
    },
    createDownloadFile() {
      const result = this.results.map(row => ({
        "ACTION": row[0],
        "AD REF": row[1],
        "EQUIVALENT AD": row[2],
        "EXCEL SHEET LINE": row[4],
        "AD GROUP LINE": parseInt(row[3])+1,
        "COLUMN": row.length == 6 ? row[5] : "",
      }));

      // Creating the new worksheet and workbook
      this.download_worksheet = XLSX.utils.json_to_sheet(result);
      this.download_workbook = XLSX.utils.book_new();
      this.download_workbook.SheetNames.push(this.worksheetNewVersion);
      this.download_workbook.Sheets[this.worksheetNewVersion] = this.download_worksheet;
    },
    downloadFile() {
      let name = "CROSS-CHECK RESULT - " + this.fileName;
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    },
  },
  watch: {
    readyToPrint(val) {
      setTimeout(() => {if(val) document.querySelector(".printer-container").innerHTML = this.html;}, 1000);
      // setTimeout(() => document.querySelectorAll(".table").forEach(t => console.log(t.offsetHeight*0.26458333333334 >= 180)), 2000);
    },
    worksheet() {
      this.html = "";
    },
    splitIn2() {
      this.html = "";
    },
    prepared() {
      this.html = "";
    },
    checked() {
      this.html = "";
    },
    tableHeightCorrector() {
      this.html = "";
    },
    maxRowsspan() {
      this.html = "";
    },
  }
}
</script>


